const adjustRadiusBasedOnData = (ctx) => {
    // const v:any = ctx.parsed.y;
    if (ctx.dataset.spot == 'none')
        return;
    if (ctx.dataset.spot == 'last') {
        const index = ctx.dataIndex;
        if (index == 0)
            return 2;
        else if (ctx.dataset.data && !ctx.dataset.data[index + 1])
            return 5;
        else
            return 0;
    }
    /*return v < 10 ? 5
     : v < 25 ? 7
     : v < 50 ? 9
     : v < 75 ? 11
     : 15;*/
};
